"use client"

import { Search, User } from "lucide-react"
import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"

export function Header() {
  const [searchOpen, setSearchOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const [profileOpen, setProfileOpen] = useState(false)
  const router = useRouter()

  const handleSignOut = () => {
    localStorage.removeItem("user")
    localStorage.removeItem("patients")
    localStorage.removeItem("visits")
    localStorage.removeItem("clinicContacts")
    router.push("/login")
  }

  const mockSearchResults = [
    { id: 1, title: "Justin Gojar", type: "Patient", path: "/patients" },
    { id: 2, title: "Jecel Garbin", type: "Patient", path: "/patients" },
    { id: 3, title: "Dashboard", type: "Page", path: "/dashboard" },
    { id: 4, title: "Visit Records", type: "Page", path: "/visit-records" },
  ]

  const filteredResults = searchQuery
    ? mockSearchResults.filter((result) => result.title.toLowerCase().includes(searchQuery.toLowerCase()))
    : []

  return (
    <header className="bg-white border-b border-gray-200 px-8 py-4 flex items-center justify-between">
      <div className="flex-1 max-w-md relative">
        <Search className="absolute left-3 top-3 text-gray-400" size={20} />
        <input
          type="text"
          placeholder="Search..."
          value={searchQuery}
          onChange={(e) => {
            setSearchQuery(e.target.value)
            setSearchOpen(true)
          }}
          onFocus={() => setSearchOpen(true)}
          className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#8B3A3A]"
        />
        {searchOpen && searchQuery && (
          <div className="absolute top-12 left-0 right-0 bg-white border border-gray-200 rounded-lg shadow-lg z-50">
            {filteredResults.length > 0 ? (
              filteredResults.map((result) => (
                <Link
                  key={result.id}
                  href={result.path}
                  onClick={() => {
                    setSearchOpen(false)
                    setSearchQuery("")
                  }}
                  className="block px-4 py-3 hover:bg-gray-50 border-b last:border-b-0"
                >
                  <p className="font-medium text-gray-900">{result.title}</p>
                  <p className="text-xs text-gray-500">{result.type}</p>
                </Link>
              ))
            ) : (
              <div className="px-4 py-3 text-gray-500 text-sm">Not found or not available</div>
            )}
          </div>
        )}
      </div>

      <div className="flex items-center gap-6 ml-8">
        <div className="relative">
          <button
            onClick={() => setProfileOpen(!profileOpen)}
            className="w-10 h-10 bg-[#8B3A3A] rounded-full flex items-center justify-center text-white font-bold hover:bg-[#6D2E2E]"
          >
            <User size={20} />
          </button>
          {profileOpen && (
            <div className="absolute right-0 top-12 bg-white border border-gray-200 rounded-lg shadow-lg w-48">
              <Link href="/profile" className="block px-4 py-2 hover:bg-gray-50 border-b">
                My Profile
              </Link>
              <button onClick={handleSignOut} className="w-full text-left px-4 py-2 hover:bg-gray-50 text-red-600">
                Sign Out
              </button>
            </div>
          )}
        </div>
      </div>
    </header>
  )
}
