"use client"

import { LayoutWrapper } from "@/components/layout-wrapper"
import { Mail, Phone, Facebook, Plus, Edit2, Trash2 } from "lucide-react"
import { useState, useEffect } from "react"
import { getCurrentUser } from "@/lib/constants"

interface ContactInfo {
  id: number
  title: string
  description: string
  subtext: string
  icon: "facebook" | "email" | "phone"
  color: string
  textColor: string
  link: string
}

export default function ContactsPage() {
  const [user, setUser] = useState<any>(null)
  const [contacts, setContacts] = useState<ContactInfo[]>([])
  const [editingId, setEditingId] = useState<number | null>(null)
  const [editData, setEditData] = useState<ContactInfo | null>(null)
  const [showAddForm, setShowAddForm] = useState(false)
  const [newContact, setNewContact] = useState<Partial<ContactInfo>>({
    title: "",
    description: "",
    subtext: "",
    icon: "email",
    color: "bg-red-100",
    textColor: "text-[#8B3A3A]",
    link: "",
  })

  useEffect(() => {
    const currentUser = getCurrentUser()
    setUser(currentUser)

    const savedContacts = localStorage.getItem("clinicContacts")
    if (savedContacts) {
      setContacts(JSON.parse(savedContacts))
    } else {
      setContacts([])
    }
  }, [])

  const isAdmin = user?.role === "admin"

  const handleAddContact = () => {
    if (!newContact.title?.trim() || !newContact.description?.trim()) return

    const contact: ContactInfo = {
      id: Date.now(),
      title: newContact.title as string,
      description: newContact.description as string,
      subtext: newContact.subtext || "",
      icon: (newContact.icon || "email") as "facebook" | "email" | "phone",
      color: newContact.color as string,
      textColor: newContact.textColor as string,
      link: newContact.link || "",
    }

    const updated = [...contacts, contact]
    setContacts(updated)
    localStorage.setItem("clinicContacts", JSON.stringify(updated))
    setShowAddForm(false)
    setNewContact({
      title: "",
      description: "",
      subtext: "",
      icon: "email",
      color: "bg-red-100",
      textColor: "text-[#8B3A3A]",
      link: "",
    })
  }

  const handleEditContact = () => {
    if (!editData) return

    const updated = contacts.map((c) => (c.id === editingId ? editData : c))
    setContacts(updated)
    localStorage.setItem("clinicContacts", JSON.stringify(updated))
    setEditingId(null)
    setEditData(null)
  }

  const handleDeleteContact = (id: number) => {
    const updated = contacts.filter((c) => c.id !== id)
    setContacts(updated)
    localStorage.setItem("clinicContacts", JSON.stringify(updated))
  }

  const getIcon = (iconName: string) => {
    switch (iconName) {
      case "facebook":
        return Facebook
      case "email":
        return Mail
      case "phone":
        return Phone
      default:
        return Mail
    }
  }

  return (
    <LayoutWrapper>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-[#8B3A3A] mb-2">Contact Us</h1>
            <p className="text-gray-600">Get in touch with SorSU Medical Clinic</p>
          </div>
          {isAdmin && (
            <button
              onClick={() => setShowAddForm(!showAddForm)}
              className="flex items-center gap-2 px-6 py-3 rounded-lg font-semibold bg-[#8B3A3A] text-white hover:bg-[#6D2E2E] transition-colors"
            >
              <Plus size={20} />
              Add Contact
            </button>
          )}
        </div>

        {/* Add Contact Form */}
        {showAddForm && isAdmin && (
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 space-y-4">
            <h2 className="text-xl font-bold text-[#8B3A3A]">Add New Contact</h2>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-semibold mb-2">Title</label>
                <input
                  type="text"
                  value={newContact.title || ""}
                  onChange={(e) => setNewContact({ ...newContact, title: e.target.value })}
                  placeholder="e.g., Facebook Page"
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                />
              </div>
              <div>
                <label className="block text-sm font-semibold mb-2">Icon Type</label>
                <select
                  value={newContact.icon || "email"}
                  onChange={(e) => setNewContact({ ...newContact, icon: e.target.value as any })}
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                >
                  <option value="facebook">Facebook</option>
                  <option value="email">Email</option>
                  <option value="phone">Phone</option>
                </select>
              </div>
            </div>
            <div>
              <label className="block text-sm font-semibold mb-2">Contact Value</label>
              <input
                type="text"
                value={newContact.description || ""}
                onChange={(e) => setNewContact({ ...newContact, description: e.target.value })}
                placeholder="e.g., clinic@sorsu.edu.ph"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold mb-2">Note (Optional)</label>
              <input
                type="text"
                value={newContact.subtext || ""}
                onChange={(e) => setNewContact({ ...newContact, subtext: e.target.value })}
                placeholder="e.g., Follow us for updates"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg"
              />
            </div>
            <div>
              <label className="block text-sm font-semibold mb-2">Link</label>
              <input
                type="text"
                value={newContact.link || ""}
                onChange={(e) => setNewContact({ ...newContact, link: e.target.value })}
                placeholder="e.g., mailto:clinic@sorsu.edu.ph"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg"
              />
            </div>
            <div className="flex gap-2">
              <button
                onClick={handleAddContact}
                className="bg-[#8B3A3A] text-white px-6 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E]"
              >
                Add Contact
              </button>
              <button
                onClick={() => setShowAddForm(false)}
                className="bg-gray-200 text-gray-900 px-6 py-2 rounded-lg font-semibold"
              >
                Cancel
              </button>
            </div>
          </div>
        )}

        {/* Contact Cards */}
        <div className="grid grid-cols-1 gap-6">
          {contacts.map((contact) => {
            const Icon = getIcon(contact.icon)
            return (
              <div key={contact.id} className="space-y-0">
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-8 hover:shadow-md transition-shadow">
                  <div className="flex items-start justify-between">
                    <a
                      href={contact.link}
                      className="flex items-start gap-6 flex-1 group hover:no-underline cursor-pointer"
                    >
                      <div
                        className={`w-20 h-20 ${contact.color} rounded-lg flex items-center justify-center flex-shrink-0 group-hover:scale-110 transition-transform`}
                      >
                        <Icon className={`${contact.textColor}`} size={32} />
                      </div>
                      <div>
                        <h3 className="text-2xl font-bold text-gray-900 mb-2">{contact.title}</h3>
                        <p className={`${contact.textColor} font-semibold mb-1`}>{contact.description}</p>
                        {contact.subtext && <p className="text-gray-600 text-sm">{contact.subtext}</p>}
                      </div>
                    </a>

                    {isAdmin && (
                      <div className="flex gap-2 ml-4 flex-shrink-0">
                        <button
                          onClick={() => {
                            setEditingId(contact.id)
                            setEditData({ ...contact })
                          }}
                          className="bg-blue-600 text-white px-3 py-2 rounded-lg font-semibold hover:bg-blue-700 flex items-center justify-center"
                          title="Edit"
                        >
                          <Edit2 size={18} />
                        </button>
                        <button
                          onClick={() => handleDeleteContact(contact.id)}
                          className="bg-red-600 text-white px-3 py-2 rounded-lg font-semibold hover:bg-red-700 flex items-center justify-center"
                          title="Delete"
                        >
                          <Trash2 size={18} />
                        </button>
                      </div>
                    )}
                  </div>
                </div>

                {editingId === contact.id && isAdmin && editData && (
                  <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 space-y-4 mt-4">
                    <h2 className="text-xl font-bold text-[#8B3A3A]">Edit Contact</h2>
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-semibold mb-2">Title</label>
                        <input
                          type="text"
                          value={editData.title}
                          onChange={(e) => setEditData({ ...editData, title: e.target.value })}
                          placeholder="e.g., Facebook Page"
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-semibold mb-2">Icon Type</label>
                        <select
                          value={editData.icon || "email"}
                          onChange={(e) => setEditData({ ...editData, icon: e.target.value as any })}
                          className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                        >
                          <option value="facebook">Facebook</option>
                          <option value="email">Email</option>
                          <option value="phone">Phone</option>
                        </select>
                      </div>
                    </div>
                    <div>
                      <label className="block text-sm font-semibold mb-2">Contact Value</label>
                      <input
                        type="text"
                        value={editData.description}
                        onChange={(e) => setEditData({ ...editData, description: e.target.value })}
                        placeholder="e.g., clinic@sorsu.edu.ph"
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-semibold mb-2">Note (Optional)</label>
                      <input
                        type="text"
                        value={editData.subtext}
                        onChange={(e) => setEditData({ ...editData, subtext: e.target.value })}
                        placeholder="e.g., Follow us for updates"
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-semibold mb-2">Link</label>
                      <input
                        type="text"
                        value={editData.link}
                        onChange={(e) => setEditData({ ...editData, link: e.target.value })}
                        placeholder="e.g., mailto:clinic@sorsu.edu.ph"
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                      />
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={handleEditContact}
                        className="bg-[#8B3A3A] text-white px-6 py-2 rounded-lg font-semibold hover:bg-[#6D2E2E]"
                      >
                        Save Changes
                      </button>
                      <button
                        onClick={() => {
                          setEditingId(null)
                          setEditData(null)
                        }}
                        className="bg-gray-200 text-gray-900 px-6 py-2 rounded-lg font-semibold"
                      >
                        Cancel
                      </button>
                    </div>
                  </div>
                )}
              </div>
            )
          })}
        </div>
      </div>
    </LayoutWrapper>
  )
}
